@flag-info: url(data:image/svg+xml;base64,);
@flag-default: url(data:image/svg+xml;base64,);

@logo-full-primary: url(data:image/svg+xml;base64,);
@logo-full-inverse: url(data:image/svg+xml;base64,);


.logo,
.logo-primary,
.logo-full-primary  {
	background-image: @logo-full-primary;
	background-repeat: no-repeat;
	background-position: 50% 50%;

	&.logo-inverse,
	&.logo-full-inverse {
		background-image: @logo-full-inverse;
	}
}


.flag {
	background-repeat: no-repeat;
	background-position: 50% 50%;
	background-image: @flag-default;

	&.flag-info {
		background-image: @flag-info;
	}
}
